<template>
  <div class="hs-container" v-if="!noValue(bookInit)">
    <c-header leftIcon1="home_2.png" :leftFn1="_gotoHomePage" :title="$t('base.TrainTicketConfirmation')"></c-header>
    <hs-scroll
      class="table-view">
        <hs-section>
          <hs-cell-1 :showLogo="false">
            <div class="cell-top">
              <div class="cell-top-one">
                <p>{{Number(trainOrder.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')}}</p>
              </div>
              <div class="cell-top-two">
                <p class="infor">{{$t('base.OrderDate')}}: {{$t(beTime(new Date()), 'date', ['yyyy-MM-dd', 'MM dd, yyyy'])}}&nbsp;{{beTime(new Date(), 'hh:mm')}}</p>
              </div>
              <div  class="cell-button">
                <button v-if="Number(trainOrder.PayType) === 2 && noValue(JSON.stringify(trainOrder.VettingProcessList))" @click="_gotoSubmitAndPay">{{$t('base.SubmitAndPay')}}</button>
                <button v-else @click="_createOrder">{{$t('base.Submit')}}</button>
              </div>
            </div>
          </hs-cell-1>
        </hs-section>
        <train-route @delegateForTableTime="_clickTableTime" @delegateNotice="_getNoticeRules" :route='trainOrder.GoSelectedRoute'></train-route>
        <train-route @delegateForTableTime="_clickTableTime" @delegateNotice="_getNoticeRules" :route='trainOrder.BackSelectedRoute'></train-route>
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in trainOrder.Passengers" :key="index">
          <div class="custom-cell-people">
            <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
              <p>{{$t('base.Passenger')}}</p>
            </div>
            <div class="right" :style="{'border-bottom-width': index === trainOrder.Passengers.length - 1 ? '0px' : '1px'}">
              <!-- <div class="name">{{item.Name}}</div>
              <div class="others">
                <p>{{item.Credential.CredentialName}} {{item.Credential.CredentialNo}}</p>
                <p>手机号 {{item.Mobile}}</p>
              </div> -->
              <p>{{item.Name}}</p>
              <p>{{item.Credential.CredentialName}} {{item.Credential.CredentialNo}}</p>
              <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
            </div>
          </div>
        </hs-cell-1>
        </hs-section>
        <hs-section>
          <hs-cell-small-title v-if="bookInit && bookInit.IsDisplayCustomItem && trainOrder.CustomItem && trainOrder.CustomItem.Name"
            :title="bookInit.CustomItemName"
            type="bold"
            :value="trainOrder.CustomItem.Name"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="bookInit && bookInit.IsDisplayPurpose"
            :title="$t('base.TravelPurpose')"
            type="bold"
            :value="trainOrder.Purpose"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="bookInit && bookInit.IsDisplayAuthorizationCode"
            :title="$t('base.AuthorizationCode')"
            type="bold"
            :value="trainOrder.AuthorizationCode"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(trainOrder.TravelType) === 0" @click.native="showExamineProcess = true"
            :class="{disabled: noValue(JSON.stringify(trainOrder.VettingProcessList))}"
            :title="$t('base.ApprovalInformation')"
            type="bold"
            :value="noValue(JSON.stringify(trainOrder.VettingProcessList)) ? $t('base.NoApprovalRequired') : (language === 'CN' ? '' : $t('base.ApprovalInformation'))"
            :showLogo="!noValue(JSON.stringify(trainOrder.VettingProcessList))">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="Number(trainOrder.TravelType) === 0"
            :title="$t('base.PaymentWay')"
            type="bold"
            :value="trainOrder.PayType | filterPayType"
            :showLogo="false">
          </hs-cell-small-title>
          <hs-cell-small-title v-if="trainOrder.ViolationRankReason"
            :title="$t('base.ViolationOfRank')"
            type="bold"
            :value="trainOrder.ViolationRankReason"
            :warning="false"
            :showLogo="false"
            @header="_getTravelStandard">
          </hs-cell-small-title>
        </hs-section>
        <hs-section>
          <hs-cell-1 :showLogo="false" :showSeparator="false" v-for="(item, index) in trainOrder.Contacts" :key="index">
          <div class="custom-cell-people">
            <div class="left" :style="{opacity: index === 0 ? 1 : 0}">
              <p>{{$t('base.Contacts')}}</p>
            </div>
            <div class="right" :style="{'border-bottom-width': index === trainOrder.Contacts.length - 1 ? '0px' : '1px'}">
              <p>{{item.Name}}</p>
              <p>{{$t('base.CellphoneNumber')}} {{item.Mobile ? item.Mobile : $t('base.Unfilled')}}</p>
              <p>{{$t('base.Email')}} {{item.Email ? item.Email : $t('base.Unfilled')}}</p>
            </div>
          </div>
        </hs-cell-1>
        </hs-section>
    </hs-scroll>
    <div class="footer" @click="showPriceDetail=!showPriceDetail">
      <div class="footer-price">
        <p>{{$t('base.TotalAmount')}}</p>
        <p>¥{{totalPrice}}</p>
      </div>
      <div class="footer-price-detail">
        <div class="logo" :style="{transform: showPriceDetail ? 'rotate(90deg)' : 'rotate(-90deg)'}" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
      </div>
    </div>
    <hs-dialog v-model="showPriceDetail" height="calc(100% - 50px)" ref="dialog">
      <hs-scroll class="showPriceDetail">
        <hs-cell-1 :showLogo="false" v-if="bookInit.GoChargeInfo">
        <div class="footer-price-cell">
          <p>{{trainOrder.GoSelectedRoute?trainOrder.GoSelectedRoute.TypeName : $t('base.Price')}}<template v-if="bookInit.BackChargeInfo">({{$t('base.Going')}})</template></p>
          <p><span>¥{{bookInit.GoChargeInfo.SeatPrice}}</span><span>x {{trainOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
        </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false" v-if="bookInit.BackChargeInfo">
            <div class="footer-price-cell">
              <p>{{trainOrder.BackSelectedRoute?trainOrder.BackSelectedRoute.TypeName : $t('base.Price')}}({{$t('base.Return')}})</p>
              <p><span>¥{{bookInit.BackChargeInfo.SeatPrice}}</span><span>x {{trainOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
            </div>
        </hs-cell-1>
        <hs-cell-1 :showLogo="false">
          <div class="footer-price-cell">
            <p>{{$t('base.ServiceFee')}}</p>
            <p><span>￥{{serviceCharge}}</span><span>x {{trainOrder.Passengers.length}}{{$t('base.QuantifierPeople')}}</span></p>
          </div>
        </hs-cell-1>
      </hs-scroll>
    </hs-dialog>
    <c-examine-process v-model="showExamineProcess" :details="trainOrder" :passengerWarning="$t('base.Passenger')"></c-examine-process>
    <c-table-time v-if="showTableTime" :params="currentTableTime"></c-table-time>
    <hs-dialog v-model="showRulesDialog">
      <hs-scroll class="rulesDialog" :data="[rulesHtml]">
        <ul>
          <div v-html="rulesHtml"></div>
        </ul>
      </hs-scroll>
    </hs-dialog>
    <c-travel-standard v-model="showTravelStandard" :details="trainRight.TrainRank" :travelPolicy="trainRight.TravelPolicy" :type="3"></c-travel-standard>

    <c-order-success ref="COrderSuccess" :XOrder="trainOrder" :OrderBusinessType="10"></c-order-success>
    <pay ref="Pay"></pay>
  </div>
</template>
<script type='text/ecmascript-6'>
import Vue from 'vue'
import CHeader from 'components/c-header/c-header'
import COrderSuccess from 'components/c-order-success/c-order-success.vue'
import HsTab from 'base/hs-tab/hs-tab'
import HsScroll from 'base/hs-scroll/hs-scroll'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsCellSmallTitle from 'base/hs-tableview/hs-cell-small-title/hs-cell-small-title'
import HsNull from 'base/hs-null/hs-null'
import HsDialog from 'base/hs-dialog/hs-dialog'
import TrainRoute from 'pages/train/book/train-route';
import TrainOrderParams from 'common/model/train/trainOrderParams'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import Pay from 'pages/pay/pay'
import { mapGetters, mapMutations, mapActions } from 'vuex'
import axios from 'axios'
import { kpiTrainSaveOrder } from 'api';
export default {
  beforeRouteEnter(to, from, next) {
    // 判断是否有值  无值返回首页    （这里在提交订单成功时需要清空vuex）
    next(vm => {
      if (vm.noValue(vm.trainOrder.TravelType) || vm.noValue(vm.bookInit)) {
        vm.$router.replace('/home')
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    if (this.showExamineProcess || this.showTableTime || this.showPriceDetail || this.showRulesDialog) {
      this.showExamineProcess = false
      this.showTableTime = false
      this.showPriceDetail = false
      this.showRulesDialog = false
      next(false)
    } else {
      next()
    }
  },
  computed: {
    ...mapGetters([
      'trainOrder',
      'isMiniprogram',
      'trainRight'
    ]),
    bookInit() {
      return this.$route.params.details
    },
    // 服务费
    serviceCharge() {
      let goServiceChange = this.bookInit && this.bookInit.GoChargeInfo && this.bookInit.GoChargeInfo.ServiceCharge ? this.bookInit.GoChargeInfo.ServiceCharge : 0
      let backServiceChange = this.bookInit && this.bookInit.BackChargeInfo && this.bookInit.BackChargeInfo.ServiceCharge ? this.bookInit.BackChargeInfo.ServiceCharge : 0
      return goServiceChange + backServiceChange
    },
    // 总价
    totalPrice() {
      let goSeatPrice = this.bookInit && this.bookInit.GoChargeInfo && this.bookInit.GoChargeInfo.SeatPrice ? this.bookInit.GoChargeInfo.SeatPrice : 0
      let backSeatPrice = this.bookInit && this.bookInit.BackChargeInfo && this.bookInit.BackChargeInfo.SeatPrice ? this.bookInit.BackChargeInfo.SeatPrice : 0
      return (goSeatPrice + backSeatPrice + this.serviceCharge) * this.trainOrder.Passengers.length
    }
  },
  filters: {
    filterPayType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.CompanyPayment')
        case 2:
          return Vue.prototype.$t('base.PersonalPayment')
        case 3:
          return `${Vue.prototype.$t('base.CompanyPayment')}(${Vue.prototype.$t('base.Prepayments')})`
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  data() {
    return {
      showPriceDetail: false,
      showExamineProcess: false,
      showTableTime: false,
      showRulesDialog: false,
      rulesHtml: '',
      currentTableTime: {},
      showTravelStandard: false
    }
  },
  methods: {
    ...mapMutations([
      'SET_TRAIN_ORDER'
    ]),
    ...mapActions([
      'dispatchTrainRight'
    ]),
    _getNoticeRules() {
      let url = '/static/html/train_notice.html'
      axios.get(url).then((res) => {
        this.rulesHtml = res.data
        this.showRulesDialog = true
      })
    },
    _createOrder() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitYourOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = new TrainOrderParams(this.trainOrder);
          kpiTrainSaveOrder(params).then(res => {
            console.log('保存订单: ', res)
            if (res.ResultData.IsSuccess) {
              /** 提交成功后 */
              let trainOrder = JSON.parse(JSON.stringify(this.trainOrder))
              trainOrder.Ids = res.ResultData.Ids
              this.SET_TRAIN_ORDER(trainOrder)
              this.$refs.COrderSuccess.open()
              // this.$alert({
              //   content: this.$t('TM.OrderSubmissionSuccessful'),
              //   confirmTxt: this.$t('base.CheckOrder'),
              //   cancelTxt: this.$t('base.ContinueBooking'),
              //   confirmFn: () => {
              //     this.$router.push({name: 'TrainOrderInforList', params: {TravelType: this.trainOrder.TravelType}})
              //   },
              //   cancelFn: () => {
              //     this.$router.replace({path: '/train/query', query: {TravelType: this.trainOrder.TravelType}})
              //   }
              // })
              /********************************************************************************************************************/
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    // 当前点击时刻表
    _clickTableTime(item) {
      this.currentTableTime = {
        DepartStationCode: item.DepartStationCode,
        DepartStationName: item.DepartStationName,
        ArrivalStationCode: item.ArrivalStationCode,
        ArrivalStationName: item.ArrivalStationName,
        DepartDate: item.DepartureDateTime,
        TrainNo: item.TrainNo,
        TrainCode: item.TrainCode
      }
      this.showTableTime = true;
    },
    _gotoHomePage() {
      this.$router.replace('/home')
    },
    // 提交并支付
    _gotoSubmitAndPay() {
      this.$alert({
        content: this.$t('TM.AreYouSureToSubmitAndPayForTheOrder'),
        cancelTxt: this.$t('base.Cancel'),
        confirmFn: () => {
          let params = new TrainOrderParams(this.trainOrder);
          console.log(params);
          kpiTrainSaveOrder(params).then(res => {
            console.log('保存订单: ', res)
            if (res.ResultData.IsSuccess) {
              this._gotoPay(res.ResultData.Ids[0])
            } else {
              this.$toast(this.$t('TM.TheSubmissionFailedPleaseTryAgainLater'))
            }
          })
        }
      })
    },
    /** 支付 */
    _gotoPay(Id) {
      this.$refs.Pay && this.$refs.Pay.gotoPay({
        OrderBusinessType: 10,
        OrderID: Id,
        PayWay: 3,
        TravelType: Number(this.trainOrder.TravelType),
        CallbackPathName: 'TrainOrderInforList'
      })
    },
    // 获取差旅职级
    _getTravelStandard() {
      this.dispatchTrainRight().then((res) => {
        console.log('trainRight: ', this.trainRight)
        this.showTravelStandard = true
      })
    }
  },
  components: {
    CHeader,
    COrderSuccess,
    HsTab,
    HsScroll,
    HsSection,
    HsCell1,
    HsCellSmallTitle,
    HsNull,
    HsDialog,
    TrainRoute,
    CExamineProcess: () => import('components/c-examine-process/c-examine-process-confirm'),
    CTableTime: () => import('components/c-table-time/c-table-time'),
    CTravelStandard,
    Pay
  }
}
</script>
<style lang='stylus' scoped rel='stylesheet/stylus'>
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
     width 100%
     .table-view
      width 100%
      height calc(100% - 95px)
      overflow hidden
     .cell-top
        width 100%
        padding 10px 0px
        @extend .flex-column
        .cell-top-one
          @extend .flex-row
          line-height 1.6em
          >p
            &:nth-child(1)
              font-size 1 rem
              color $color_black_text_1
        >.cell-top-two
          @extend .flex-column
          @extend .flex-start
          @extend .flex-top
          line-height 1.2em
          width calc(100% - 80px)
          @extend .flex-column
          >p
            &:nth-child(1)
              font-size 0.8 rem
              color $color_gray_text
        >.cell-button
          @extend .flex-column
          width 100%
         >button
          width 100%
          margin-top 10px
          background-color $color_theme
          color $color_white
          border-radius 5px
          padding 5px
          font-size 1 rem
    .cell-segment-one
      @extend .flex-row
      @extend .flex-between
      width calc(100%)
      padding 15px 10px
      >p
        &:nth-child(1)
          font-size 0.8 rem
          color $black_6
      .cell-segment-button
        >button
          font-size 0.6rem
          color $color_gray_text
          padding 0 8px
          height 20px
          background-color $color_white
          &:nth-child(1)
            margin 0 5px
    .cell-passenger
      width 100%
      >.cell-passenger-top
        @extend .flex-row
        @extend .flex-between
        >p
          &:nth-child(1)
            font-size 1 rem
            color $color_black_text_1
    .cell-passenger-list
      line-height 1.2em
      width 100%
      @extend .flex-column
      padding 10px 0
      >p
        &:nth-child(1)
          font-size 1 rem
          color $color_black_text_1
          font-weight bold
        &:nth-child(2)
          color $color_black_text_1
          font-size 0.8rem
          color $color_gray_text
        &:nth-child(3)
          color $color_black_text_1
          font-size 0.8rem
          color $color_gray_text
    .cell-review
       color $color_gray_text
    .footer
      width 100%
      height 50px
      padding 0 15px
      background-color $color_white
      @extend .flex-row
      @extend .flex-between
      border-top 1px solid $color_gray_layer
      >.footer-price
        @extend .flex-row
        @extend .flex-start
        width calc(100% - 30px)
        height 100%
        >p
          &:nth-child(1)
            font-size 1 rem
          &:nth-child(2)
            margin-left 10px
            color $color_black_text_1
            font-size 1 rem
            color $color_theme
            font-weight bold
      >.footer-price-detail
        @extend .flex-row
        @extend .flex-between
        width 20px
        height 100%
        >p
          &:nth-child(1)
            font-size 0.9rem
        >.logo
          width 20px
          height 20px
          background-size 100% 100%
          transition all 300ms
  .showPriceDetail
    position absolute !important
    background-color $color_white
    width 100%
    bottom 0
    max-height 70%
    .footer-price-cell
      @extend .flex-row
      @extend .flex-between
      width 100%
      >p
        font-size 0.8 rem
        color $color_black_text_0
        span
          &:nth-child(1)
            color $color_theme
          &:nth-child(2)
            margin-left 5px
  .custom-cell-people
      @extend .flex-between
      @extend .flex-start
      @extend .flex-top
      width 100%
      >.left
        width 90px
        padding 10px 0
        >p
          @extend .flex-row
          @extend .flex-start
          font-size 1rem
          color $color_black_text_1
          line-height 1.2em
      >.right
        width calc(100% - 100px)
        padding 10px 0
        border-bottom 1px dashed $color_gray_layer
        line-height 1.2em
        @extend .flex-column
        >p
          &:nth-child(1)
            color $color_black_text_0
            font-size 1 rem
            line-height 1.4em
            font-weight bold
          &:nth-child(2)
            margin-top 7px
            font-size 1rem
            line-height 1.5em
            color $color_gray_text
          &:nth-child(3)
            font-size 1rem
            line-height 1.5em
            color $color_gray_text
  .rulesDialog
    width 100%
    height 100%
  .cell-3-value
    font-weight bold
  .cell-3-title
    @extend .flex-row
    @extend .flex-start
    width 100%
    >.logo
      width 20px
      height 20px
      background-size 100% 100%
      margin-left 3px
</style>



