export default class TrainOrderParams {
  constructor(details) {
    this.Purpose = details && details.Purpose ? details.Purpose : '' // 差旅目的
    this.AppointVettingPersonID = details && details.AppointVettingPersonID ? details.AppointVettingPersonID : '' // 选择的指定审批人ID
    this.AppointVettingPersonName = details && details.AppointVettingPersonName ? details.AppointVettingPersonName : '' // 选择的指定审批人姓名
    this.ViolationRankName = details && details.ViolationRankName ? details.ViolationRankName : '' // 违反差旅政策姓名
    this.ViolationRankReason = details && details.ViolationRankReason ? details.ViolationRankReason : '' // 违反差旅政策原因
    this.DepartStationCode = details && details.DepartStationCode ? details.DepartStationCode : '' // 出发城市
    this.ArrivalStationCode = details && details.ArrivalStationCode ? details.ArrivalStationCode : '' // 到达城市
    this.GoSelectedTrainInfo = details && details.GoSelectedRoute.TrainCode !== '' ? {
      TrainCode: details.GoSelectedRoute.TrainCode,
      SeatTypeCode: details.GoSelectedRoute.TypeCode,
      DepartDate: details.GoSelectedRoute.DepartureDateTime,
      ChooseSeats: details.GoSelectedRoute.ChooseSeats
    } : null // 去程选择的车次信息
    this.BackSelectedTrainInfo = details && details.BackSelectedRoute.TrainCode !== '' ? {
      TrainCode: details.BackSelectedRoute.TrainCode,
      SeatTypeCode: details.BackSelectedRoute.TypeCode,
      DepartDate: details.BackSelectedRoute.DepartureDateTime,
      ChooseSeats: details.BackSelectedRoute.ChooseSeats
    } : null // 返程选择的车次信息
    this.CustomItem = details && details.CustomItem ? details.CustomItem : null // 自定义项(所有乘客一样)
    this.Contacts = details && details.Contacts ? details.Contacts : [] // 联系人
    this.Passengers = details && details.Passengers ? details.Passengers.map((item, index) => {
      return {
        PsgID: item.ID, // 乘客ID
        UpID: item.UpID, // 员工ID
        Email: item.Email, // 邮箱
        Name: item.Name, // 名字
        Credential: item.Credential, // 证件信息
        Department: item.Department, // 部门信息
        CostCenter: item.CostCenter, // 成本中心
        BusinessUnit: item.BusinessUnit, // BU
        Mobile: item.Mobile, // 手机号
        IsSendIssuedEmail: item.IsSendIssuedEmail, // 是否发送出票邮件
        IsSendIssuedSms: item.IsSendIssuedSms, // 是否发送出票短信
        UpType: item.UpType // 员工类型
      }
    }) : [] // 乘客
    this.IsAcptStanding = details && details.IsAcptStanding ? details.IsAcptStanding : false // 是否接受站票（无座票）
    this.TravelType = details && Number(details.TravelType) === 0 ? 0 : 1 // 因公因私
    this.PayType = details && details.PayType ? details.PayType : 0 // 支付类型
    this.DeliveryInfo = {
      ID: '',
      DeliveryType: details && details.DeliveryType ? details.DeliveryType : 3,
      DeliveryAddress: details && details.DeliveryAddress ? details.DeliveryAddress : ''
    }
  }
}



