<template>
  <hs-section v-if="!noValue(route.TrainCode)">
    <div class="segment">
      <hs-cell-1 :showLogo="false">
        <div class="cell-segment">
          <div class="cell-segment-left">
            <p>{{$t(beTime(route.DepartureDateTime), 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(beTime(route.DepartureDateTime))}}</p>
            <p>{{beTime(route.DepartureDateTime,'hh:mm')}}</p>
            <p>{{route.DepartStationName}}</p>
          </div>
          <div class="cell-segment-middle">
            <p>{{route.TrainCode}}</p>
            <div class="cell-segment-time" @click.stop="$emit('delegateForTableTime',route)">
              <div class="layer"></div>
              <div class="time">{{$t('base.Schedule')}}</div>
              <div class="layer"></div>
            </div>
            <p>{{route.RunTime}}</p>
          </div>
          <div class="cell-segment-right">
            <p>{{$t(beTime(route.ArrivalDateTime), 'date', ['MM月dd日', 'MM dd'])}} {{getWeeks(beTime(route.ArrivalDateTime))}}</p>
            <p>{{beTime(route.ArrivalDateTime,'hh:mm')}}<span class="ArrivalDays" v-if="route.ArrivalDays > 0">+{{route.ArrivalDays}}</span></p>
            <p>{{route.ArrivalStationName}}</p>
          </div>
        </div>
      </hs-cell-1>
      <slot>
        <hs-cell-1 :showLogo="false" @click.native="$emit('delegateNotice')">
          <div class="cell-segment-one" :type="language">
            <p>{{route.TypeName}}</p>
            <a>{{$t('base.BookingInstructionsAndInstructionsForReturning')}}</a>
          </div>
        </hs-cell-1>
      </slot>
    </div>
  </hs-section>
</template>
<script type='text/ecmascript-6'>
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsScroll from 'base/hs-scroll/hs-scroll'
export default {
  components: {
    HsCell1,
    HsSection,
    HsScroll
  },
  props: {
    route: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return {
      detail: ''
    }
  },
  methods: {}
}
</script>
<style lang='stylus' scoped='' rel='stylesheet/stylus'>
@import '~common/stylus/base'
@import '~common/stylus/variable'
.segment
  width 100%
  height calc(100% - 95px)
  overflow hidden
  background-color $color_white
  // margin-top 10px
  .cell-segment
    @extend .flex-row
    @extend .flex-between
    width 100%
    padding 15px 0
    .cell-segment-left
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      line-height 1.4em
      >p
        &:nth-child(1)
          font-size 0.8 rem
          color $color_black_text_1
        &:nth-child(2)
          font-size 1.2 rem
          // color $color_black_text_1
          font-weight bold
        &:nth-child(3)
          font-size 0.8 rem
          color $color_black_text_1
    .cell-segment-middle
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      line-height 1.4em
      >p
        &:nth-child(1)
          font-size 0.8 rem
          font-weight bold
        &:nth-child(3)
          font-size 0.6 rem
          color $color_gray_text
      >.cell-segment-time
        @extend .flex-center
        @entend .flex-row
        >.layer
          @extend .flex-center
          height 1px
          width 10px
          background-color $color_gray_text
        >.time
          @extend .flex-center
          font-size 0.6rem
          color $color_gray_text
          padding 0 8px
          height 20px
          border-radius 10px
          border 1px solid $color_gray_text
          background-color $color_white
    .cell-segment-right
      @extend .flex-column
      @extend .flex-center
      width 33.33%
      line-height 1.4em
      >p
        &:nth-child(1)
          font-size 0.8 rem
          color $color_black_text_1
        &:nth-child(2)
          font-size 1.2 rem
          // color $color_black_text_1
          font-weight bold
          position relative
        &:nth-child(3)
          font-size 0.8 rem
          color $color_black_text_1
      .ArrivalDays
        color $color_theme
        font-size 0.8rem
        position absolute
        top -4px
        right -16px
  .cell-segment-one
    @extend .flex-row
    @extend .flex-between
    width 100%
    padding 15px 0
    >p
      font-size 0.8 rem
      color $black_6
    >a
      font-size 0.8 rem
      color $color_black_text_1
  .cell-segment-one[type="EN"]
    @extend .flex-column
    @extend .flex-top
    >p
      margin-bottom 5px
</style>



